import { Button, ButtonProps, Container, ContainerProps } from "@mui/material";
import {
  DragDropContext,
  Draggable,
  Droppable,
  OnDragEndResponder,
} from "react-beautiful-dnd";
import { useAppDispatch, useAppSelector } from "../../../../app/hooks";
import { CounterName } from "../../../../features/database/constants";
import {
  selectCounterIds,
  selectCounterTotal,
} from "../../../../features/database/selector";
import {
  addCounter,
  setCounterIndex,
} from "../../../../features/database/slice";
import "./DefaultView.scss";
import Entry from "./entry/Entry";
import TotalEntry from "./total-entry/TotalEntry";

const buttonProps: ButtonProps = {
  fullWidth: true,
  variant: "contained",
  color: "inherit",
  sx: {
    color: "black",
  },
  children: "Add " + CounterName,
};

const defaultContainerProps: ContainerProps = {
  className: "default-view-root",
  style: {
    display: "flex",
    flexDirection: "column",
  },
  maxWidth: "md",
};

function DefaultView() {
  const dispatch = useAppDispatch();
  const counterIds = useAppSelector(selectCounterIds);
  const totalCount = useAppSelector(selectCounterTotal);

  const onAddCounter = () => {
    dispatch(addCounter());
  };

  const containerProps: ContainerProps = {
    ...defaultContainerProps,
    style: {
      ...defaultContainerProps.style,
      justifyContent: counterIds.length === 0 ? "center" : "flex-start",
    },
  };

  const onDragEnd: OnDragEndResponder = (result) => {
    const { draggableId, destination } = result;

    if (destination) {
      dispatch(
        setCounterIndex({
          counterId: Number(draggableId),
          index: destination.index,
        })
      );
    }
  };

  return (
    <DragDropContext onDragEnd={onDragEnd}>
      <Droppable droppableId="test">
        {(droppableProvided) => (
          <Container
            ref={droppableProvided.innerRef}
            {...containerProps}
            {...droppableProvided.droppableProps}
          >
            {counterIds.length === 0 && (
              <Button {...buttonProps} onClick={onAddCounter} />
            )}

            {counterIds.length !== 0 && <TotalEntry count={totalCount} />}

            {counterIds.map((counterId, index) => (
              <Draggable
                key={counterId}
                draggableId={String(counterId)}
                index={index}
              >
                {(draggableProvided) => (
                  <span
                    ref={draggableProvided.innerRef}
                    {...draggableProvided.draggableProps}
                    {...draggableProvided.dragHandleProps}
                    style={{
                      ...draggableProvided.draggableProps.style,
                      marginBottom: 8,
                      cursor: "default",
                    }}
                  >
                    <Entry counterId={counterId} index={index} />
                  </span>
                )}
              </Draggable>
            ))}
            {droppableProvided.placeholder}
          </Container>
        )}
      </Droppable>
    </DragDropContext>
  );
}

export default DefaultView;
