import { PayloadAction } from "@reduxjs/toolkit";
import { Counter, Database } from "./types";

export function createCounter(): Counter {
  return {
    counterId: Date.now(),
    name: "",
    count: 0,
  };
}

// database

export function createDatabase(): Database {
  return {
    counters: {},
    counterIds: [],
    timestamps: [0],
    version: 1,
  };
}

export function withDatabaseReducer<T = unknown>(
  reducer: (state: Database, action: PayloadAction<T>) => void
) {
  return (state: Database, action: PayloadAction<T>) => {
    reducer(state, action);

    state.timestamps.unshift(Date.now());

    if (state.timestamps.length > 70) {
      state.timestamps.pop();
    }
  };
}
