import { createSlice, PayloadAction } from "@reduxjs/toolkit";
import { databaseDataName } from "../../constants";
import { loadFromLocalStorage } from "../../storage/utils";
import { insertValueToArray, removeValueFromArray } from "../../utils/utils";
import { CounterPayload, Database } from "./types";
import { createCounter, createDatabase, withDatabaseReducer } from "./utils";

const databaseSlice = createSlice({
  name: "database",
  initialState: loadFromLocalStorage(databaseDataName, createDatabase()),
  reducers: {
    setDatabase: (state, action: PayloadAction<Database>) => {
      Object.assign(state, action.payload);
    },
    addCounter: withDatabaseReducer<
      | {
          payload?: CounterPayload;
          index?: number;
        }
      | undefined
    >((state, action) => {
      const newCounter = createCounter();

      state.counters[newCounter.counterId] = newCounter;

      if (action.payload) Object.assign(newCounter, action.payload);

      insertValueToArray(
        newCounter.counterId,
        state.counterIds,
        action.payload?.index
      );
    }),
    setCounterIndex: withDatabaseReducer<{
      counterId: number;
      index: number;
    }>((state, action) => {
      insertValueToArray(
        action.payload.counterId,
        state.counterIds,
        action.payload.index
      );
    }),
    setCounter: withDatabaseReducer<{
      counterId: number;
      payload?: CounterPayload;
    }>((state, action) => {
      const counter = state.counters[action.payload.counterId];

      Object.assign(counter, action.payload.payload);
    }),
    deleteCounter: withDatabaseReducer<number>((state, action) => {
      removeValueFromArray(action.payload, state.counterIds);

      delete state.counters[action.payload];
    }),
    resetAllCounters: withDatabaseReducer((state, action) => {
      Object.values(state.counters).forEach((counter) => (counter.count = 0));
    }),
    clearAllCounters: withDatabaseReducer((state, action) => {
      state.counterIds = [];
      state.counters = {};
    }),
  },
});

export const {
  setDatabase,
  addCounter,
  setCounter,
  setCounterIndex,
  deleteCounter,
  resetAllCounters,
} = databaseSlice.actions;

export default databaseSlice;
