import { DialogContentText } from "@mui/material";
import PopUpDialog from "../popup-dialog/PopUpDialog";

export interface PopUpConfirmDialogProps {
  open: boolean;

  title: string;
  description: string;
  actions: React.ReactNode;

  onClose?: () => void;
}

function PopUpConfirmDialog(props: PopUpConfirmDialogProps) {
  return (
    <PopUpDialog
      open={props.open}
      title={props.title}
      content={<DialogContentText>{props.description}</DialogContentText>}
      actions={props.actions}
      onClose={props.onClose}
    />
  );
}

export default PopUpConfirmDialog;
