import {
  Divider,
  ListItemIcon,
  ListItemText,
  Menu,
  MenuItem,
} from "@mui/material";
import { useState } from "react";
import { PopUpMenuItem, PopUpMenuProps } from "./PopUpMenuTypes";

function renderItem(key: any, props: PopUpMenuItem, onClose: () => void) {
  switch (props.type) {
    case "button":
      return (
        <MenuItem
          key={key}
          onClick={() => {
            props.onClick?.();
            onClose();
          }}
          disabled={props.disabled}
        >
          {props.icon && (
            <ListItemIcon>
              <props.icon fontSize="small" />
            </ListItemIcon>
          )}
          <ListItemText>{props.name}</ListItemText>
        </MenuItem>
      );
    case "divider":
      return <Divider key={key} />;
  }
}

function PopUpMenu(props: PopUpMenuProps) {
  const [anchorEl, setAchorEl] = useState<HTMLElement | null>(null);
  const [open, setOpen] = useState(false);

  const onOpen = () => {
    setOpen(true);
  };

  const onClose = () => {
    setOpen(false);
  };

  return (
    <>
      {props.children(setAchorEl, onOpen)}
      <Menu
        disableRestoreFocus
        disableEnforceFocus
        anchorEl={anchorEl}
        open={open}
        onClose={onClose}
        anchorOrigin={props.anchorOrigin}
        transformOrigin={props.transformOrigin}
      >
        {props.items.map((itemProps, index) =>
          renderItem(itemProps.key ? itemProps.key : index, itemProps, onClose)
        )}
      </Menu>
    </>
  );
}

export default PopUpMenu;
