import { RootState } from "../../app/store";

export const selectCounterIds = (state: RootState) => state.database.counterIds;

export const selectCounterById = (counterId: number) => (state: RootState) =>
  state.database.counters[counterId];

export const selectCounterTotal = (state: RootState) => {
  let total = 0;
  Object.values(state.database.counters).forEach(
    (counter) => (total += counter.count)
  );

  return total;
};
