import { Button, ButtonProps, Typography } from "@mui/material";
import { useState } from "react";
import { useAppDispatch, useAppSelector } from "../../../../../app/hooks";
import { store } from "../../../../../app/store";
import { selectCounterTotal } from "../../../../../features/database/selector";
import { resetAllCounters } from "../../../../../features/database/slice";
import PopUpConfirmDialog from "../../../../common/modal/popup-confirm-dialog/PopUpConfirmDialog";
import "./TotalEntry.scss";

export interface TotalEntryProps {
  count: number;
}

const buttonProps: ButtonProps = {
  color: "inherit",
};

function copyCounters() {
  const state = store.getState();

  const counterInfos: Array<{ name: string; count: number }> = [];

  [...state.database.counterIds].forEach((counterId) => {
    const counter = state.database.counters[counterId];

    counterInfos.push({
      name: counter.name,
      count: counter.count,
    });
  });

  const total = selectCounterTotal(state);

  let text = `Total (${total})\n\n`;
  counterInfos.forEach((info) => (text += `(${info.count}) ${info.name}\n`));

  navigator.clipboard.writeText(text);
}

function TotalEntry(props: TotalEntryProps) {
  const dispatch = useAppDispatch();
  const totalCount = useAppSelector(selectCounterTotal);

  const [resetDialogOpen, setResetDialogOpen] = useState(false);

  const onCloseReset = () => setResetDialogOpen(false);
  const onOpenReset = () => setResetDialogOpen(true);

  const onResetCounter = () => {
    dispatch(resetAllCounters(null));
    onCloseReset();
  };

  return (
    <div className="total-entry-root">
      <Typography variant="h6">{`Total (${props.count})`}</Typography>
      <div className="total-entry-row">
        <Button {...buttonProps} onClick={copyCounters}>
          {"Copy"}
        </Button>
        <Button
          {...buttonProps}
          disabled={totalCount === 0}
          onClick={onOpenReset}
        >
          {"Reset"}
        </Button>
      </div>

      <PopUpConfirmDialog
        open={resetDialogOpen}
        title={"Reset All"}
        description={"Reset all counters?"}
        actions={
          <>
            <Button onClick={onCloseReset}>{"Cancel"}</Button>
            <Button
              color="inherit"
              variant="contained"
              style={{
                color: "black",
              }}
              onClick={onResetCounter}
            >
              {"Reset"}
            </Button>
          </>
        }
        onClose={onCloseReset}
      />
    </div>
  );
}

export default TotalEntry;
