export function joinClassNames(...classNames: Array<any>) {
  const composition: Array<string> = [];

  for (let i = 0; i < arguments.length; i++) {
    const arg = arguments[i];

    if (typeof arg === "string" && arg !== "") {
      composition.push(arg);
    }
  }

  return composition.join(" ");
}

export function removeValueFromArray(value: any, array: Array<any>) {
  const index = array.indexOf(value);

  if (index >= 0) {
    array.splice(index, 1);
  }
}

export function containValueInsideArray(value: any, array: Array<any>) {
  const index = array.indexOf(value);

  return index >= 0;
}

export function insertValueToArray(
  value: any,
  array: Array<any>,
  index?: number
) {
  removeValueFromArray(value, array);

  if (index !== undefined) array.splice(index, 0, value);
  else array.push(value);
}
