import { store } from "../app/store";
import { databaseDataName } from "../constants";
import { saveToLocalStorage } from "./utils";

let savedState = store.getState();

store.subscribe(() => {
  const currentState = store.getState();

  // local save
  if (currentState.database !== savedState.database) {
    saveToLocalStorage(databaseDataName, currentState.database);
  }

  savedState = currentState;
});
