import { CSSProperties, useEffect, useState } from "react";
import { joinClassNames } from "../../../../utils/utils";
import "./EditableText.scss";

export type EditableTextConfirm = (
  text: string,
  isInitialization: boolean
) => void;

export type EditableTextCancel = (isInitialization: boolean) => void;

export interface EditableTextProps {
  text: string;
  editing: boolean;
  onFinishEditing: () => void;

  disableAutoSelect?: boolean;
  placeholder?: string;

  onConfirm?: EditableTextConfirm;
  onCancel?: EditableTextCancel;

  onClick?: React.MouseEventHandler<HTMLSpanElement>;

  className?: string;
  style?: CSSProperties;
}

function EditableText(props: EditableTextProps) {
  // vars
  const isInitialization = props.text === "";

  // text
  const [textValue, setTextValue] = useState(props.text);

  useEffect(() => {
    setTextValue(props.text);
  }, [props.text]);

  const onTextChange: React.ChangeEventHandler<HTMLInputElement> = (ev) => {
    setTextValue(ev.target.value);
  };

  // autoselect
  const [inputRef, setInputRef] = useState<HTMLInputElement | null>(null);

  useEffect(() => {
    if (!Boolean(props.disableAutoSelect) && inputRef) {
      inputRef.select();
    }
  }, [props.disableAutoSelect, inputRef]);

  // process result
  const onCancel = () => {
    setTextValue(props.text);
    props.onCancel?.(isInitialization);
    props.onFinishEditing?.();
  };

  const onConfirm = (isInitialization: boolean) => {
    const trimmedValue = textValue.trim();

    if (trimmedValue === "" || trimmedValue === props.text) {
      onCancel();
    } else {
      props.onConfirm?.(trimmedValue, isInitialization);
      props.onFinishEditing?.();
    }
  };

  // events
  const onKeyDown: React.KeyboardEventHandler = (ev) => {
    if (ev.key === "Enter") {
      onConfirm(isInitialization);
      ev.preventDefault();
    } else if (ev.key === "Escape") {
      onCancel();
      ev.preventDefault();
    } else if (ev.key === "Backspace" && textValue === "" && isInitialization) {
      onCancel();
      ev.preventDefault();
    }
  };

  const onBlur: React.FocusEventHandler = (ev) => {
    onConfirm(false);
  };

  if (props.editing)
    return (
      <input
        ref={setInputRef}
        autoFocus
        placeholder={props.placeholder}
        value={textValue}
        onChange={onTextChange}
        style={props.style}
        onKeyDown={onKeyDown}
        onBlur={onBlur}
        className={joinClassNames("editable-text-input", props.className)}
      />
    );
  return (
    <span
      style={props.style}
      className={joinClassNames("editable-text-input", props.className)}
      onClick={props.onClick}
    >
      {props.text}
    </span>
  );
}

export default EditableText;
