import { Button } from "@mui/material";
import PopUpConfirmDialog from "../popup-confirm-dialog/PopUpConfirmDialog";

export interface PopUpDeleteDialogProps {
  open: boolean;

  title: string;
  description: string;

  cancelText?: string;
  deleteText?: string;

  onDelete?: () => void;
  onClose?: () => void;
}

function PopUpDeleteDialog(props: PopUpDeleteDialogProps) {
  const onDelete = () => {
    props.onDelete?.();
    props.onClose?.();
  };

  return (
    <PopUpConfirmDialog
      open={props.open}
      title={props.title}
      description={props.description}
      actions={
        <>
          <Button onClick={props.onClose}>
            {props.cancelText ? props.cancelText : "Cancel"}
          </Button>
          <Button color="error" variant="contained" onClick={onDelete}>
            {props.deleteText ? props.deleteText : "Delete"}
          </Button>
        </>
      }
      onClose={props.onClose}
    />
  );
}

export default PopUpDeleteDialog;
