import {
  ArrowDownward as ArrowDownwardIcon,
  ArrowUpward as ArrowUpwardIcon,
  Delete as DeleteIcon,
  Edit as EditIcon,
  MoreVert as MoreVertIcon,
  RestartAlt as RestartAltIcon,
} from "@mui/icons-material";
import { Button, ButtonProps, Card, IconButton } from "@mui/material";
import { useEffect, useState } from "react";
import { useAppDispatch, useAppSelector } from "../../../../../app/hooks";
import { CounterName } from "../../../../../features/database/constants";
import { selectCounterById } from "../../../../../features/database/selector";
import {
  addCounter,
  deleteCounter,
  setCounter,
} from "../../../../../features/database/slice";
import EditableText, {
  EditableTextCancel,
  EditableTextConfirm,
} from "../../../../common/input/editable-text/EditableText";
import PopUpDeleteDialog, {
  PopUpDeleteDialogProps,
} from "../../../../common/modal/popup-dialog-delete/PopUpDeleteDialog";
import PopUpMenu from "../../../../common/modal/popup-menu/PopUpMenu";
import { PopUpMenuProps } from "../../../../common/modal/popup-menu/PopUpMenuTypes";
import "./Entry.scss";

// increment rgb(35, 169, 99)
// decrement rgb(219, 74, 74)

export interface EntryProps {
  counterId: number;
  index: number;
}

const transitionDuration = 125;

const buttonProps: ButtonProps = {
  variant: "outlined",
  color: "inherit",
  fullWidth: true,
};

function Entry(props: EntryProps) {
  const dispatch = useAppDispatch();
  const counter = useAppSelector(selectCounterById(props.counterId));

  // add counter
  const onAddCounter = (indexOffset: number) => {
    dispatch(
      addCounter({
        index: props.index + indexOffset,
      })
    );
  };

  // editable text
  const [editing, setEditing] = useState(counter.name === "");

  const onConfirm: EditableTextConfirm = (text, isInitialization) => {
    dispatch(
      setCounter({
        counterId: props.counterId,
        payload: {
          name: text,
        },
      })
    );

    if (isInitialization) onAddCounter(1);
  };

  const onEdit = () => {
    setEditing(true);
  };

  const onFinishEditing = () => {
    setEditing(false);
  };

  const onCancel: EditableTextCancel = (isInitialization) => {
    if (isInitialization) dispatch(deleteCounter(props.counterId));
  };

  // counter manipulation
  const [color, setColor] = useState<string | undefined>();

  useEffect(() => {
    if (color !== undefined) {
      const timeout = setTimeout(() => {
        setColor(undefined);
      }, transitionDuration * 2);

      return () => clearTimeout(timeout);
    }
  }, [color]);

  const onIncrement = () => {
    dispatch(
      setCounter({
        counterId: props.counterId,
        payload: {
          count: counter.count + 1,
        },
      })
    );
    setColor("rgb(35, 169, 99)");
  };

  const onDecrement = () => {
    if (counter.count > 0) {
      dispatch(
        setCounter({
          counterId: props.counterId,
          payload: {
            count: counter.count - 1,
          },
        })
      );
      setColor("rgb(219, 74, 74)");
    }
  };

  const onResetCount = () => {
    dispatch(
      setCounter({
        counterId: props.counterId,
        payload: {
          count: 0,
        },
      })
    );
    setColor("rgb(150, 150, 150)");
  };

  // delete
  const [deleteDialogOpen, setDeleteDialogOpen] = useState(false);

  const onDelete = () => {
    dispatch(deleteCounter(props.counterId));
  };

  const deleteDialogProps: PopUpDeleteDialogProps = {
    title: `Delete ${CounterName}`,
    description: `Delete ${CounterName} "${counter.name}"?`,
    open: deleteDialogOpen,
    onClose: () => setDeleteDialogOpen(false),
    onDelete: onDelete,
  };

  // menu
  const popUpMenuProps: PopUpMenuProps = {
    children: (setRef, onOpen) => (
      <IconButton ref={setRef} onClick={onOpen}>
        <MoreVertIcon />
      </IconButton>
    ),
    items: [
      {
        type: "button",
        name: "Edit",
        icon: EditIcon,
        onClick: onEdit,
      },
      {
        type: "button",
        name: "Reset",
        disabled: counter.count === 0,
        icon: RestartAltIcon,
        onClick: onResetCount,
      },
      {
        type: "divider",
      },
      {
        type: "button",
        name: "Add Above",
        icon: ArrowUpwardIcon,
        onClick: () => onAddCounter(0),
      },
      {
        type: "button",
        name: "Add Below",
        icon: ArrowDownwardIcon,
        onClick: () => onAddCounter(1),
      },
      {
        type: "divider",
      },
      {
        type: "button",
        name: "Delete",
        icon: DeleteIcon,
        onClick: () => setDeleteDialogOpen(true),
      },
    ],
    anchorOrigin: {
      horizontal: "right",
      vertical: "bottom",
    },
    transformOrigin: {
      horizontal: "right",
      vertical: "top",
    },
  };

  const onDisabledClick: React.MouseEventHandler = (ev) => {
    ev.stopPropagation();
  };

  return (
    <Card className="entry-root" variant="outlined">
      <div className="entry-row">
        <EditableText
          text={counter.name}
          editing={editing}
          placeholder={"Type counter name here..."}
          onConfirm={onConfirm}
          onFinishEditing={onFinishEditing}
          onCancel={onCancel}
        />
        <span className="entry-row" onClick={onDisabledClick}>
          <PopUpMenu {...popUpMenuProps} />
          <PopUpDeleteDialog {...deleteDialogProps} />
        </span>
      </div>
      <div className="entry-counter">
        <Button {...buttonProps} onClick={onDecrement}>
          {"Subtract"}
        </Button>
        <span
          className="entry-count"
          style={{
            borderColor: color,
            color: color,
            transition: `border-color ${transitionDuration}ms, color ${transitionDuration}ms`,
          }}
        >
          {counter.count}
        </span>
        <Button {...buttonProps} onClick={onIncrement}>
          {"Add"}
        </Button>
      </div>
    </Card>
  );
}

export default Entry;
